import React from 'react'

function TamilKural({ item }) {
    return (
        <div className='kural'>
            <p className='kural_no'>{item?.Number}</p>
            <p><b>{item?.Line1}<br />{item?.Line2}</b></p>
            <p><b></b>: <i>{item?.mv}</i></p>
            <p><b>.  </b>: {item?.mk}</p>
        </div>
    )
}

export default TamilKural